import React, { useState, useEffect, useRef } from "react";
import { Form } from "react-bootstrap";
import styles2 from "../css/COLUMN.module.css";
import ElemDeleteControl from "./ElemDeleteControlWrapper";
import styles from "../css/Label.module.css";
import EditableLabel from "../../ReusableComponents/EditableLabel/EditableLabel";
import QuickTools from "../../ReusableComponents/QuickTools/QuickTools";
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import { handleDisplayProperty } from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import { GetState, GetStateFromData } from "../../components/CustomHooks/CustomHooks";
import cloneDeep from 'lodash-es/cloneDeep';

const LABEL = (props) => {
  const data = props.data;
  const labelPropStyle = props?.data?.style || {
    Label: "",
    Visible: "",
    MobileVisible: "",
    TabsVisible: "",
    LabelAlignment: "",
    FontFamily: "",
    FontSize: "",
    FontWeight: "",
    FontColor: "",
    BackColor: "",
    BackgroundColor: ""
  };
    
  const contexts = GetStateFromData(props.containerId);

  const {controlSelected,tempCanWidth} = GetState("controlSelected","tempCanWidth");
  const isSubformInPage = props?.isSubformInPage || false;

  const { IdPrecedence, deleteControl, openQuickToolFor, formState2, device, showAlertConfirmNotifications } = contexts.contexts;
  const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false

  const actualControlId = props.data.id;
  const mappingId = props?.data?.["style"]?.["MappingId"];
  const userDefinedId = props?.data?.["style"]?.["CustomId"];
  const userDefinedControlId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);

  const themeData = formState2.theme;
  const controlThemeData = formState2.theme?.["LabelStyle"] || {};

  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);
  const [defaultLabel, setDefaultLabel] = useState(labelPropStyle?.["Label"]);
  const [editLabel, setEditLabel] = useState(false);
  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2?.style) {
      startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
      endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
      startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
      endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"]
  ]);

  useEffect(() => setDefaultLabel(props?.data?.style?.["Label"]), [props?.data?.style?.["Label"]]);

  useEffect(() => {
    if(props?.previewmode)
    handleDisplayProperty(props, device, setMobileDisplay, setTabDisplay, startMobile.current, endMobile.current, startTab.current, endTab.current)
  },[props?.data?.style?.["MobileVisible"], props?.data?.style?.["TabsVisible"], device, endMobile, endTab, startMobile, startTab,tempCanWidth]);

  const defaultStyles = {
    font_family: "Open Sans",
    font_size: "14px",
    font_weight: "normal",
    font_color: "#000000",
    back_color: "inherit",
    font_style: "normal",
    text_align: "left"
  };
  const [rerenderState, setReRenderState] = useState(false);
  const labelDefaultStyles = useRef(defaultStyles);

  const spacingStyles = useRef({
    padding_left: "0px",
    padding_right: "0px",
    padding_top: "0px",
    padding_bottom: "0px"
  });

  const setLabelStyles = (newStyle) => {
    const prevState = JSON.stringify(labelDefaultStyles.current)
    labelDefaultStyles.current = { ...labelDefaultStyles.current, ...newStyle };
    if(!props.previewmode) { 
      if(prevState !== JSON.stringify(labelDefaultStyles.current)) {
        setReRenderState(!rerenderState);
      }
    }
  };

  const setSpacingStyles = (newStyle) => {
    const prevState = JSON.stringify(spacingStyles.current)
    spacingStyles.current = { ...spacingStyles.current, ...newStyle };
    if(!props.previewmode) {  
      if(prevState !== JSON.stringify(spacingStyles.current)) {
        setReRenderState(!rerenderState);
      }
    }
  };


  useEffect(() => {
    if (themeData?.["BasicTheme"]?.["FontFamily"] && !controlThemeData?.["FontFamily"]) {
      setLabelStyles({
        font_family:
          themeData["BasicTheme"]["FontFamily"] === "Select" ? "Open Sans" : themeData["BasicTheme"]["FontFamily"]
      });
    }
    if (themeData?.["BasicTheme"]?.["FontSize"] && !controlThemeData?.["FontSize"]) {
      setLabelStyles({ font_size: themeData["BasicTheme"]["FontSize"] });
    }
  }, [themeData?.["BasicTheme"], labelPropStyle?.["renderTheme"]]);

  useEffect(() => {
      if (!labelPropStyle?.["FontWeight"]) {
        if (controlThemeData["FontWeight"] == "Normal") {
          setLabelStyles({ font_weight: "normal" });
          setLabelStyles({ font_style: "normal" });
        } else if (controlThemeData["FontWeight"] == "Bold") {
          setLabelStyles({ font_weight: "bold" });
          setLabelStyles({ font_style: "normal" });
        } else if (controlThemeData["FontWeight"] == "Italic") {
          setLabelStyles({ font_style: "italic" });
          setLabelStyles({ font_weight: "normal" });
        } else if (controlThemeData["FontWeight"] == "Bold Italic") {
          setLabelStyles({ font_weight: "bold" });
          setLabelStyles({ font_style: "italic" });
        } else {
          setLabelStyles({ font_weight: defaultStyles.font_weight });
          setLabelStyles({ font_style: defaultStyles.font_style });
        }
      }

      if (controlThemeData["FontColor"] && !labelPropStyle?.["FontColor"]) {
        setLabelStyles({ font_color: controlThemeData["FontColor"] });
      }
      if (controlThemeData["FontSize"] && !labelPropStyle?.["FontSize"]) {
        setLabelStyles({
          font_size:
            controlThemeData["FontSize"] === "Select"
              ? themeData?.["BasicTheme"]?.["FontSize"] || defaultStyles.font_size
              : controlThemeData["FontSize"]
        });
      }
      if (controlThemeData["FontFamily"] && !labelPropStyle?.["FontFamily"]) {
        setLabelStyles({
          font_family:
            controlThemeData["FontFamily"] === "Select"
              ? themeData?.["BasicTheme"]?.["FontFamily"] === "Select"
                ? defaultStyles.font_family
                : themeData?.["BasicTheme"]?.["FontFamily"] || defaultStyles.font_family
              : controlThemeData["FontFamily"]
        });
      }
      if (controlThemeData["BackgroundColor"] && !labelPropStyle?.["BackgroundColor"]) {
        setLabelStyles({ back_color: controlThemeData["BackgroundColor"] });
      }
      if (controlThemeData["LabelAlignment"] && !labelPropStyle?.["TextAlignment"]) {
        setLabelStyles({ text_align: controlThemeData["LabelAlignment"] });
      }
  }, [controlThemeData, labelPropStyle?.["renderTheme"]]);

  useEffect(() => {
    if (labelPropStyle?.["FontColor"]) {
      setLabelStyles({ font_color: labelPropStyle["FontColor"] });
    }
    if (labelPropStyle?.["FontFamily"]) {
      setLabelStyles({ font_family: labelPropStyle["FontFamily"] });
    }
    if (labelPropStyle?.["FontSize"]) {
      setLabelStyles({ font_size: labelPropStyle["FontSize"] });
    }
    if (labelPropStyle?.["FontWeight"]) {
      if(labelPropStyle["FontWeight"] ==="Regular Italic"){
        setLabelStyles({font_weight: 300});
        setLabelStyles({font_style: "italic"});
       }
      else if(labelPropStyle["FontWeight"]==="Semibold"){
        setLabelStyles({font_weight: 600});
        setLabelStyles({font_style: "normal"});
      }
      else if(labelPropStyle["FontWeight"]==="Bold"){
          setLabelStyles({font_weight: "bold"});
          setLabelStyles({font_style: "normal"});
      }
      else if(labelPropStyle["FontWeight"]==="Regular"){
          setLabelStyles({font_weight: 300});
          setLabelStyles({font_style: "normal"});
      }
      else if(labelPropStyle["FontWeight"]==="Bold Italic"){
        setLabelStyles({font_weight: "bold"});
        setLabelStyles({font_style: "italic"});
      }
      else if(labelPropStyle["FontWeight"]==="Light Italic"){
        setLabelStyles({font_weight: 100});
        setLabelStyles({font_style: "italic"});
      }
      else if(labelPropStyle["FontWeight"]==="Semibold Italic"){
        setLabelStyles({font_weight: 600});
        setLabelStyles({font_style: "italic"});
      }
      else if(labelPropStyle["FontWeight"]==="Light"){
        setLabelStyles({font_weight: 100});
        setLabelStyles({font_style: "normal"});
      }
      else setLabelStyles({ font_weight: labelPropStyle["FontWeight"] });
    }
    if (labelPropStyle?.["FontStyle"]) {
      setLabelStyles({ font_style: labelPropStyle["FontStyle"] });
    }
    if (labelPropStyle?.["BackColor"]) {
      setLabelStyles({ back_color: labelPropStyle["BackColor"] });
    }
    if (labelPropStyle?.["TextAlignment"]) {
      setLabelStyles({ text_align: labelPropStyle["TextAlignment"] });
    }
    if (labelPropStyle?.["SpacingLeft"]) {
      setSpacingStyles({ padding_left: labelPropStyle?.["SpacingLeft"] });
    }
    if (labelPropStyle?.["SpacingRight"]) {
      setSpacingStyles({ padding_right: labelPropStyle?.["SpacingRight"] });
    }
    if (labelPropStyle?.["SpacingTop"]) {
      setSpacingStyles({ padding_top: labelPropStyle?.["SpacingTop"] });
    }
    if (labelPropStyle?.["SpacingBottom"]) {
      setSpacingStyles({ padding_bottom: labelPropStyle?.["SpacingBottom"] });
    }
  }, [cloneDeep((labelPropStyle))]);

  const inputStyle = {
    color: labelDefaultStyles.current.font_color,
    fontSize: labelDefaultStyles.current.font_size,
    fontFamily: labelDefaultStyles.current.font_family,
    fontWeight: labelDefaultStyles.current.font_weight,
    fontStyle: labelDefaultStyles.current.font_style,
    backgroundColor: labelDefaultStyles.current.back_color,
    display: "block",
    lineHeight: "normal",
    "word-break": "break-word",
    "white-space":"break-spaces"
  };

  const lang= window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA";

  function getAlignValues(){
    if(lang){
      if(labelDefaultStyles.current.text_align.toLowerCase()==="left"){
        return "right"
      }
      else if(labelDefaultStyles.current.text_align.toLowerCase()==="left"){
        return "left"
      }
      else return "center"
    }
    else return labelDefaultStyles.current.text_align
    
  }

  const inputStyleGroup = {
    textAlign: getAlignValues(),
    backgroundColor: labelDefaultStyles.current.back_color,
    paddingLeft: spacingStyles.current.padding_left,
    paddingRight: spacingStyles.current.padding_right,
    paddingTop: spacingStyles.current.padding_top,
    paddingBottom: spacingStyles.current.padding_bottom,
    display: "block"
  };

  const bringEditableLabel = () => setEditLabel(true);

  const setLabelHandler = (recievedLabel) => {
    try {
      if (!recievedLabel) {
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: "label is required",
          autoHideDuration: 6000
        });
      } else if (labelPropStyle?.Label) {
        setDefaultLabel((prevDefaultLabel) => {
          if (prevDefaultLabel === recievedLabel) {
            return prevDefaultLabel;
          } else {
            props.data.style["Label"] = recievedLabel;
            return recievedLabel;
          }
        });
        setEditLabel(false);
      }
    } catch (err) {
      console.error(err);
    }
  };

  const [, setRenderState] = useState(true);
  useEffect(() => {
    setRenderState(prev => (!prev));
  },[])

  if (props.previewmode) {
    return (
      <>
        {labelPropStyle?.["Visible"] === "true" ? (
          <Form.Group
            id={userDefinedControlId + "_group"}
            className={styles.borderStylePreview + " spacingThemeControls "}
            style={mobileDisplay === false || tabDisplay === false ? { display: "none" } : inputStyleGroup} 
            control_id={userDefinedControlId + "_group"}
            controlId="dob"
            onDragStart={props.handleReorderStart}
            onDrop={props.handleReorderDrop}
            data-placement="top"
            key={data.id}
            elem_type="label"
            frameindex={props.data.frameIndex}
            elementindex={props.data.elementIndex}
            sheetIndex={props.data.sheetIndex}
            sheetElementIndex={props.data.sheetElementIndex}
            sheetFrameElementindex={props.data.sheetFrameElementindex}
          >
            <Form.Label
              style={inputStyle}
              elem_type="label"
              id={userDefinedControlId}
              control_id={userDefinedControlId}
              frameindex={props.data.frameIndex}
              elementindex={props.data.elementIndex}
              className={" spacingThemeLabel "}
            >
              {labelPropStyle?.["Label"]}
            </Form.Label>
          </Form.Group>
        ) : null}
      </>
    );
  } else {
    return (
      <Form.Group
        style={inputStyleGroup}
        onClick={isSubformInPage ? {} : (e) => props.handleSelected(e, data.id, "n")}
        
        className={styles2.formStyle + " spacingThemeControls "}
        control_id={data.id}
        tabIndex="1"
        controlId="dob"
        draggable={true}
        onDragStart={props.handleReorderStart}
        onDrop={props.handleReorderDrop}
        data-placement="top"
        id={userDefinedControlId + "_group"}
        key={data.id}
        elem_type="label"
        frameindex={props.data.frameIndex}
        elementindex={props.data.elementIndex}
        sheetIndex={props.data.sheetIndex}
        sheetElementIndex={props.data.sheetElementIndex}
        sheetFrameElementindex={props.data.sheetFrameElementindex}
        onKeyDown={(e) => props.handleSelected(e)}
      >
        <EditableLabel
          defaultLabel={
            <Form.Label
              onDoubleClick={bringEditableLabel}
              style={inputStyle}
              elem_type="label"
              id={userDefinedControlId}
              control_id={data.id}
              className={" spacingThemeLabel "}
              frameindex={props.data.frameIndex}
              elementindex={props.data.elementIndex}
            >
              {labelPropStyle?.["Label"]}
            </Form.Label>
          }
          editlabel={editLabel}
          default_value={defaultLabel}
          maxLength={true}
          setLabelHandler={(recievedLabel) => setLabelHandler(recievedLabel)}
          style={inputStyle}
        />
{controlSelected?.id === props?.id &&
        <ElemDeleteControl
          isSubformInPage={isSubformInPage}
          containerId={props.containerId}
          id={props.data.id}
          deleteControl={deleteControl}
          elemType={props.data.elem_type}
          setDragging={props.setDragging}
          handleReorderStart={props.handleReorderStart}
          handleReorderDrop={props.handleReorderDrop}
          data={props.data}
          dragId={props.dragId}
          dragType={"column"}
        />
}
        {
          window?.openQuickTools && open &&  
          <QuickTools containerId={props.containerId} selectedControl={props.data} open={open} setOpen={setOpen} />
        }
      </Form.Group>
    );
  }
};

export default LABEL;
